.input {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 6.94rem;
    margin-bottom: 6.33rem;
  }

  .container {
    padding: auto;
    width: 100%;
    align-content: center;
  }

  .flex {
    display: flex;
    align-content: center;
    justify-content: space-evenly;
  }
  
  .input input {
    width: 287px;
    padding: .88rem 0;
    border: 1px solid #fff;
    border-radius: 3px;
    margin-bottom: 2.22rem;
    color: #010c3f;
    text-align: center;
    font-size: 1rem;
  }
  
  .input input:focus {
    outline: none;
  }
  
  .inputButton {
    cursor: pointer;
    width: 8.11rem;
    padding: .77rem 0;
    border-radius: 54px;
    background-color: #BBD237;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: #fff;
    font-weight: 500;
  }
  
  .inputButton:hover {
    background-color: #7c8b25;
  }